/* General styles */
.instruction-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(401px at 50.6% -0.3%, rgba(255, 255, 255, 0.885) 1.2%, rgb(36, 212, 219) 100.2%);
  display: flex;
  align-items: center;
  justify-content: center;
}

.instruction-container {
  background-color: #f7f7f7;
  padding: 40px;
  border-radius: 20px;
  max-width: 80%;
  max-height: 80%;
  overflow-y: auto;
  display: grid;
  grid-gap: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  animation: popup 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

/* Keyframes animation */
@keyframes popup {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.instruction-container h2{
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  font-size: 1.8rem;
}
/* Text styles */
.instruction-container h2,
.instruction-container h4,
.instruction-container p {
  color: #333;
  text-align: justify;
}

.ZI-details-container h4 {
  margin-top: 0;
  font-size: 24px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

/* Details container styles */
.ZI-details-container {
  text-align: left;
}

.ZI-details-container p {
  margin-bottom: 20px;
  line-height: 1.6;
  text-align: justify;
}

/* Continue button styles */
.ZI-continue-button {
  padding: 15px 40px;
  font-size: 18px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  justify-self: center;
  margin-top: 30px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.ZI-continue-button:hover {
  background-color: #0056b3;
}

/* Footnote styles */
.footnote {
  margin-top: 30px;
  color: #666;
  font-size: 14px;
  text-align: center;
}

/* Media query for screens smaller than 768px (Tablet) */
@media screen and (max-width: 768px) {
  .instruction-container {
    padding: 20px;
  }
  .ZI-details-container h4 {
    font-size: 20px;
  }
  .ZI-details-container p {
    font-size: 16px;
    line-height: 1.5;
  }
  .ZI-continue-button {
    padding: 12px 30px;
    font-size: 16px;
    margin-top: 20px;
  }
}

/* Media query for screens smaller than 480px (Mobile) */
@media screen and (max-width: 480px) {
  .instruction-container {
    padding: 15px;
  }
  .ZI-details-container h4 {
    font-size: 18px;
  }
  .ZI-details-container p {
    font-size: 14px;
    line-height: 1.4;
  }
  .ZI-continue-button {
    padding: 10px 20px;
    font-size: 14px;
    margin-top: 15px;
  }
  .footnote {
    font-size: 12px;
  }
}
