/* FinalInfo.css */
.finalinfo-main {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.finalinfo-content {
 
  padding: 30px;
  background-color: #ffffff;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  width: 100%;
  position: relative;
  animation: popup 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  max-height: calc(100vh - 150px); 
  overflow-y: auto; 
}

@keyframes popup {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.label-container {
  margin-bottom: 20px;
}

.input-text {
  width: 100%;
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-sizing: border-box;
  margin-bottom: 20px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.input-text:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.finalinfo-button {
  background-color: #007bff; /* Green */
  border: none;
  color: #fff;
  padding: 15px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.finalinfo-button:hover {
  background-color: #007bff;
}

.finalinfo-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
  opacity: 0.7;
}

.finalinfo-error-message {
  color: #ff0000;
  margin-top: -10px;
  margin-bottom: 10px;
}
.label-bold {
  font-weight: bold;
}

/* Media Queries */
@media screen and (max-width: 768px) {
  .finalinfo-content {
    padding: 20px;
  }

  .input-text,
  .finalinfo-button {
    font-size: 14px;
  }
}

@media screen and (max-width: 480px) {
  .finalinfo-content {
    max-width: 90%;
    padding: 15px;
  }

  .input-text,
  .finalinfo-button {
    font-size: 12px;
  }
}
