.zakatCalculation-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  background: radial-gradient(
    401px at 50.6% -0.3%,
    rgba(255, 255, 255, 0.885) 1.2%,
    rgb(36, 212, 219) 100.2%
  );
  width: 100%;
  height: 100vh;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.zakatCalculation-main h2 {
  font-size: 35px;
  color: #fff;
  margin-bottom: 20px;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  font-weight: 600;
}

.zakatCalculation-container {
  width: 90%;
  max-width: 90%;
  background-color: #fff;
  border-radius: 30px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  padding: 40px;
  overflow-y: auto;
  max-height: calc(100vh - 250px);
  transition: box-shadow 0.3s ease;
  animation: popup 0.4s;
}

/* Keyframes animation */
@keyframes popup {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.zakatCalculation-container:hover {
  box-shadow: 0 15px 45px rgba(0, 0, 0, 0.2);
}

.zakatCalculation-section {
  margin-bottom: 30px;
}

.zakatCalculation-section h3 {
  font-size: 24px;
  margin-bottom: 30px;
  color: #333;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.zakatCalculation-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 25px 35px;
  transition: all 0.3s ease;
  border-radius: 20px;
  margin-bottom: 5px;
}

.zakatCalculation-row:last-child {
  margin-bottom: 0;
}

.itemheading {
  /* font-size: 14px; */
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  background-color: #f9f9f9;
}

.zakatCalculation-label {
  flex: 1;
  font-weight: bold;
  color: #555;
  overflow: hidden;
}

.zakatCalculation-value {
  flex: 1;
  color: #777;
  overflow: hidden;
}

.add-data-button,
.submit-button {
  padding: 15px 30px;
  background-color: #2ecc71;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  font-weight: 400;
  margin-top: 10px;
  font-size: 18px;
}

.add-data-button:hover,
.submit-button:hover {
  background-color: #27ae60;
  transform: translateY(-3px);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
}
.add-icon {
  vertical-align: middle;
  margin-right: 5px;
  font-size: 18px;
  color: #fff;
}
.zakatCalculation-delete-icon,
.zakatCalculation-edit-icon {
  cursor: pointer;
  transition: color 0.3s ease, transform 0.2s ease;
  font-size: 28px;
  color: #e74c3c;
}

.zakatCalculation-delete-icon:hover,
.zakatCalculation-edit-icon:hover {
  color: #c0392b;
  transform: scale(1.3);
}

.zakatCalculation-edit-icon {
  color: #f39c12;
  margin-right: 20px;
}

.zakatCalculation-container {
  scrollbar-width: thin;
  scrollbar-color: #3498db #f9f9f9;
}

.zakatCalculation-container::-webkit-scrollbar {
  width: 8px;
}

.zakatCalculation-container::-webkit-scrollbar-track {
  background: #f9f9f9;
}

.zakatCalculation-container::-webkit-scrollbar-thumb {
  background-color: #3498db;
  border-radius: 8px;
}

.zakatCalculation-main {
  animation: gradientAnimation 20s linear infinite alternate;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}

.entryseparation-line {
  position: relative;
  margin-bottom: 20px;
  overflow: hidden;
}
.entryseparation-line::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
}

.entryseparation-line::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(to right, #87ceeb, #4682b4);
  z-index: 2;
  transition: transform 0.3s ease-out;
  transform: scaleX(0);
}

.entryseparation-line:hover::after {
  transform: scaleX(1);
}

@media screen and (max-width: 768px) {
  .zakatCalculation-main {
    padding: 20px;
  }

  .zakatCalculation-main h2 {
    font-size: 28px;
    margin-bottom: 15px;
  }

  .zakatCalculation-container {
    width: 100%;
    max-width: 90%;
    border-radius: 20px;
    padding: 20px;
    max-height: none;
    overflow-y: auto;
  }

  .zakatCalculation-section h3 {
    font-size: 20px;
    margin-bottom: 20px;
  }

  .zakatCalculation-row {
    padding: 10px 15px;
  }

  .zakatCalculation-label {
    flex: 2;
    font-weight: bold;
    font-size: 0.5rem;
    margin: 5px;
  }

  .zakatCalculation-value {
    flex: 3;
    font-size: 0.5rem;
  }

  .add-data-button,
  .submit-button {
    padding: 12px 24px;
    font-size: 14px;
    margin-top: 10px;
  }

  .add-icon {
    font-size: 14px;
  }

  .zakatCalculation-delete-icon,
  .zakatCalculation-edit-icon {
    font-size: 24px;
  }

  .entryseparation-line {
    margin-bottom: 15px;
  }
}

@media screen and (max-width: 480px) {
  /* Styles for max-width: 480px */
  .zakatCalculation-main {
    padding: 15px;
  }

  .zakatCalculation-main h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }

  .zakatCalculation-container {
    padding: 15px;
  }

  .zakatCalculation-section h3 {
    font-size: 18px;
    margin-bottom: 15px;
  }

  .zakatCalculation-row {
    padding: 8px 12px;
  }

  .zakatCalculation-label {
    font-size: 0.4rem;
    margin: 3px;
  }

  .zakatCalculation-value {
    font-size: 0.45rem;
  }

  .add-data-button,
  .submit-button {
    padding: 10px 20px;
    font-size: 12px;
    margin-top: 8px;
  }

  .add-icon {
    font-size: 12px;
  }

  .zakatCalculation-delete-icon,
  .zakatCalculation-edit-icon {
    font-size: 20px;
  }

  .entryseparation-line {
    margin-bottom: 10px;
  }
}
