.Home-main {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: radial-gradient(401px at 50.6% -0.3%, rgba(255, 255, 255, 0.885) 1.2%, rgb(36, 212, 219) 100.2%);
  overflow: hidden;
}

.home-container {
  text-align: center;
  color: white;
}

.home-title {
  font-size: 48px;
  margin-bottom: 20px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.home-subtitle {
  font-size: 24px;
  margin-bottom: 40px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.home-start-button {
  padding: 15px 30px;
  font-size: 18px;
  background-color: #fff;
  color: #333;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}

.home-start-button:hover {
  background-color: #f5f5f5;
  color: #555;
  transform: translateY(-2px);
}

.waviy {
  position: relative;
}

.waviy span {
  position: relative;
  display: inline-block;
  font-size: 40px;
  color: #fff;
  text-transform: uppercase;
  animation: flip 2s infinite;
  animation-delay: calc(.2s * var(--i));
}

@keyframes flip {
  0%, 80% {
    transform: rotateY(360deg);
  }
}

.logo-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.trust-logo {
  width: 150px;
  height: auto;
}

.company-logo {
  width: 150px;
  height: auto;
}

@media screen and (max-width: 768px) {
  .home-title {
    font-size: 36px;
  }
  .home-subtitle {
    font-size: 18px;
    margin-bottom: 20px;
  }
  .home-start-button {
    font-size: 16px;
    padding: 10px 20px;
  }
}

@media screen and (max-width: 480px) {
  .home-title {
    font-size: 24px;
    margin-bottom: 10px;
  }
  .home-subtitle {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .home-start-button {
    font-size: 14px;
    padding: 8px 16px;
  }
}
