

.report-main {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #f5f7fa 0%, #eef1f5 100%);
  display: flex;
  justify-content: center;
  align-items: center;
}

.report-container {
  background-color: #ffffff;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  padding: 40px;
  max-width: 80%;
  width: 90%;
}
.report-header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px; /* Adjust as needed */
}

.report-username {
  flex-grow: 1; 
  text-align: center; 
  font-size: 42px; 
  font-weight: 700; 
  color: #2c3e50;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1); 
  white-space: nowrap;
  overflow: hidden; 
  text-overflow: ellipsis; 
}

.report-trust-logo {
  max-width: 100px; /* Adjust as needed */
  margin-right: 10px; /* Add some space between the trust logo and the username */
}

.report-company-logo {
  max-width: 100px; /* Adjust as needed */
  margin-left: 10px; /* Add some space between the company logo and the username */
}

.report-container h2 {
  margin: 0;
  font-size: 42px; 
  font-weight: 700; 
  color: #2c3e50;
  text-align: center;
  margin-bottom: 40px; 
  text-transform: uppercase; 
  letter-spacing: 2px; 
  line-height: 1.2; 
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1); 
}



.section {
  margin-bottom: 40px;
}

.heading-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #ecf0f3;
  padding: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  background-color: #f9f9f9;
  border-radius: 12px;
  margin-bottom: 20px;
  
 
}

.detail-heading {
  flex: 1;
  font-weight: 600;
  color: #576574;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.9rem;
  margin-right: 5px;
}

.details-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ecf0f3;
  padding: 20px;
}

.details-row:last-child {
  border-bottom: none;
}

.detail-value {
  flex: 1;
  color: #576574;
  font-size: 0.9rem;
}

.detail-value:last-child,
.detail-heading:last-child {
  text-align: right;
  font-weight: normal;
}

/* Button Style */
.download-button {
  background-color: #2ecc71;
  border: none;
  color: white;
  padding: 18px 36px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 18px;
  margin: 40px auto;
  cursor: pointer;
  border-radius: 10px;
  transition: background-color 0.3s ease, transform 0.3s ease;
  display: block;
  width: 100%;
  max-width: 240px;
}

.download-button:hover {
  background-color: #27ae60;
  transform: translateY(-2px);
}




.final-zakatvalue-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 40px;
}

.finalzakatvalue-top {
  display: flex;
  align-items: center;
}

.final-zakatvaluedetail-value {
  color: #576574;
  margin-right: 20px;
  font-size: 18px;
}

.totalzakat-value-container {
  text-align: center;
}

.totalzakat-value-container h3 {
  margin-bottom: 10px;
  font-size: 24px;
  color: #576574;
}

.totalzakat-value {
  font-size: 48px;
  font-weight: bold;
  color: #fff;
  background-color: #2c3e50;
  border-radius: 10px;
  padding: 20px 40px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
}

.totalzakat-value:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), transparent);
  z-index: -1;
  opacity: 0;
}

.report-footer {
  text-align: center;
  font-size: 12px;
  color: #576574;
  margin-top: 20px;
}

.footer-link {
  color: #576574;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-link:hover {
  color: #2c3e50;
  text-decoration: underline;
}


@media screen and (max-width: 768px) {
  .report-main {
    width: 100%;
    height: 100%;
  }

  .report-container {
    border-radius: 20px;
    padding: 20px;
    max-width: 90%;
    margin: 0 auto;
  }

  .report-header-container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
  }

  .report-username {
    font-size: 28px;
    text-align: center;
    margin: 10px 0;
  }

  .report-trust-logo,
  .report-company-logo {
    max-width: 80px;
    margin: 10px 0;
  }

  .report-container h2 {
    font-size: 32px;
    margin-bottom: 20px;
  }

  .heading-row {
    padding: 10px;
  }

  .detail-heading {
    width: 100%;
    margin-bottom: 10px;
    font-size: 0.5rem;
    flex-wrap: wrap;
    overflow: hidden; 
    text-overflow: ellipsis; 
   
  }

  .details-row {
    flex-wrap: wrap;
    padding: 10px;
  }

  .detail-value {
    width: 100%;
    margin-bottom: 10px;
    text-align: left;
    font-size: 0.5rem;
  }

  .download-button {
    padding: 14px 24px;
    margin: 20px auto;
  }

  .final-zakatvalue-container {
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }

  .finalzakatvalue-top {
    flex-direction: column;
    align-items: center;
  }

  .final-zakatvaluedetail-value {
    margin-right: 0;
    margin-bottom: 10px;
  }

  .totalzakat-value {
    padding: 14px 24px;
    font-size: 36px;
  }

  .report-footer {
    font-size: 8px;
  }
}
