.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
}

.popup-inner {
    background-color: #ffffff;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
    max-width: 600px;
    width: 90%;
    text-align: center;
    animation: popup 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@keyframes popup {
    from {
        opacity: 0;
        transform: scale(0.5);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

.eligibility-title {
    font-size: 28px;
    margin-bottom: 20px;
    color: #333;
}

.eligibility-message {
    font-size: 18px;
    margin-bottom: 20px;
    color: #666;
}

.eligibility-reasons {
    font-size: 16px;
    color: #666;
    text-align: left;
    margin-bottom: 20px;
}

.close-button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-size: 16px;
}

.close-button:hover {
    background-color: #0056b3;
}

.blessing-message {
    font-style: italic;
    color: #888;
    margin-top: 20px;
}

/* Media Queries */
@media only screen and (max-width: 768px) {
    .popup-inner {
        padding: 20px;
    }
    .eligibility-title {
        font-size: 24px;
    }
    .eligibility-message {
        font-size: 16px;
    }
    .eligibility-reasons {
        font-size: 14px;
    }
    .close-button {
        font-size: 14px;
    }
    .blessing-message {
        font-size: 14px;
    }
}
